@include media-breakpoint-down(xl) {
  .sidebar {
    &.sidebar-default {
      &.sidebar-mini {
        &.sidebar-boxed {
          transform: translateX(-130%);
        }
        transform: translateX(-100%);
      }
    }
  }
  .sidebar-default.sidebar-mini.sidebar-boxed + .main-content,
  .sidebar-default.sidebar-boxed + .main-content,
  .sidebar-default.sidebar-mini + .main-content,
  .sidebar-default + .main-content {
    margin-left: 0;
  }
  .sidebar-default {
    &.sidebar-boxed {
      & + .main-content {
        .nav {
          &.navbar {
            margin: $spacer $spacer 0 $spacer;
          }
        }
        .content-inner {
          padding: calc(var(--bs-gutter-x, 0.75rem) * 1.25);
        }
      }
    }
  }
  .search-input {
    // display: none;
  }
  .sidebar-hover {
    .logo {
      display: none !important;
    }
    .logo-hover {
      opacity: 1 !important;
      transform: translateX(0px) !important;
    }
    &:hover {
      .logo-hover {
        opacity: 1;
        transform: translateX(0px);
      }
    }
  }
}
