.sidebar-mini {
  max-width: 14.5rem;
}
.sidebar-mini .navbar-collapse .static-item {
  text-align: left;
}
.sidebar-hover .navbar-collapse .static-item .default-icon {
  display: block;
}
.sidebar-hover .navbar-collapse .static-item .mini-icon {
  display: none;
}
.sidebar-hover
  .navbar-collapse
  .navbar-nav
  .nav-item
  .nav-link:not(.disabled)
  .icon {
  display: block;
}
.sidebar-hover
  .navbar-collapse
  .navbar-nav
  .nav-item
  .nav-link:not(.disabled)
  span {
  opacity: 1;
  -webkit-transform: translateX(0) !important;
          transform: translateX(0) !important;
  transition: all 400ms ease;
  opacity: 1 !important;
}
.sidebar-default
  .navbar-collapse
  .navbar-nav
  .nav-item
  .nav-link
  i.sidenav-mini-icon {
  opacity: 0 !important;
  display: none !important;
}

.sidebar-hover.navs-rounded-all .navbar-nav .nav-item:not(.static-item),
.sidebar-hover.navs-pill-all .navbar-nav .nav-item:not(.static-item) {
  padding-left: 1rem;
}
.sidebar-hover .logo-hover {
  opacity: 1;
  -webkit-transform: translateX(-30px) !important;
          transform: translateX(-30px) !important;
}
.sidebar-hover .logo {
  -webkit-transform: translateX(-30px) !important;
          transform: translateX(-30px) !important;
  opacity: 0;
}
.sidebar.sidebar-mini + .main-content {
  margin-left: 14.5rem;
}
