.bagimg {
  position: relative;
  height: 130%;
  width: 130%;
  bottom: 350px;
  left: 100px;
}
.first-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 42%;
  top: 250px;
  transition: transform 0.5s ease-out;
}
.hover-img {
  transform: scale(1.2);
}
.hover-img:hover {
  cursor: pointer;
  transform: scale(1.4) !important;
}
.second-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 50%;
  top: 60px;
  transition: transform 0.5s ease-out;
}
.third-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 30%;
  top: 20px;
  transition: transform 0.5s ease-out;
}
.fourth-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 13%;
  top: 390px;
  transition: transform 0.5s ease-out;
}
.fifth-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 13%;
  top: 20px;
  transition: transform 0.5s ease-out;
}
.six-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 31%;
  top: 395px;
  transition: transform 0.5s ease-out;
}
.seventh-img {
  height: 150px;
  width: 150px;
  position: absolute;
  right: 25%;
  top: 220px;
  transition: transform 0.5s ease-out;
}
.eight-img {
  height: 150px;
