  transition: transform 0.5s ease-out;
}
.list-group-item {
  background-color: unset;
}
.bg-circle {
  // position: absolute;
  overflow: hidden;
  &::before {
    content: "";
    // position: absolute;
    height: 512px;
    width: 512px;
    background: rgba($primary, 0.2);
    border-radius: 50%;
    top: 0;
    right: 0;
    transform: translate(-47%, -55%) scale(2.7);
  }
}
.bg-circle-login {
  position: absolute;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    height: 512px;
    width: 512px;
    background: rgba($primary, 0.2);
    border-radius: 50%;
    top: 0;
    right: 0;
    transform: translate(-33%, -33%) scale(3.6);
  }
}

.sign-in-img {
  position: relative;
  width: 58%;
  bottom: -14%;
  left: 27%;
}
@media (min-width: 1400px) {
  .sign-in-img {
    position: relative;
    width: 53%;
    bottom: -23%;
    left: 23%;
  }
  .bg-circle-login {
    position: absolute;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      transform: translate(9%, -58%) scale(3);
    }
  }
}
@include media-breakpoint-down(xxl) {
  .sign-in-img {
    position: relative;
    width: 67%;
    bottom: -23%;
    left: 19%;
  }
  .bg-circle-login {
    position: absolute;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      transform: translate(15%, -73%) scale(3.6);
    }
  }
}

@include media-breakpoint-up(xl) {
  .bg-circle {
    &::before {
      transform: translate(-14%, -67%) scale(2.9);
    }
  }
}

